*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;font-family: 'Tajawal', monospace;
}

::placeholder {
font-family: 'Tajawal', monospace;
  color: var(--text-color) !important;
  opacity: 0.5;
}

i,
i::after,
i::before {
  font-family: 'FontAwesome';
}

p {
font-family: 'Tajawal', monospace;
  font-weight: 400;
  /*color: #4c4c4c;*/
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: var(--text-color);
font-family: 'Tajawal', monospace;
  line-height: 1.2;
}
.button {
  background-color: var(--button-color) !important;
  color: var(--text-color) !important;
  border-color: var(--border-color) !important;
  transition: background-color 0.2s linear;font-family: 'Tajawal', monospace;
}
.button:hover {
  background-color: var(--button-hover-color);
  color: var(--text-color);
  border-color: var(--border-color);
}
