@import url(https://fonts.googleapis.com/css2?family=Tajawal:wght@300;400;500;700&display=swap);
html,
body {
  height: 100% !important;
}
:root {
  background-color: transparent;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth !important;
  font-family: 'Tajawal', monospace;
  height: 100% !important;
}

.light {
  /*Light mode variables*/
  --text-color: #2b2b2b;
  --text-color2: #c5c5c5;
  --button-color: #e4e6eb;

  --button-hover-color: #d8dadf;
  --element-hover: #ececec;
  --input-inner-bg: #f0f2f5;

  --border-color: rgb(218, 210, 210, 0.5);
  --border-color2: rgba(0, 0, 0, 0.1);
  --container: #fafafa;
  --primary: rgb(245, 18, 56);
  --time-line-progress: rgb(245, 18, 56);
  --box-shadow: rgba(0, 0, 0, 0.2);

  --white: #ffffff;
  --beat-color: #ddd;

  --background: #f0f2f5;
  --love: rgb(250, 9, 49);
  --love2: rgb(255, 0, 128);
}
.dark {
  /*Dark mode variables -*/
  --text-color: #fafafa;
  --text-color2: rgb(182, 182, 182);
  --element-hover: #3a3b3c;
  --input-inner-bg: #3a3b3c;
  --border-color: rgba(255, 255, 255, 0.1);
  --border-color2: rgba(255, 255, 255, 0.1);
  --button-color: #303031;
  --button-hover-color: #444546;
  --container: #242526;

  --primary: #398bf5;
  --time-line-progress: rgb(255, 0, 128);

  --box-shadow: rgba(0, 0, 0, 0.2);

  --white: #ffffff;
  --beat-color: rgb(88, 88, 88);

  --background: #18191a;

  --love: rgb(250, 9, 49);
  --love2: rgb(255, 0, 128);
}
.App {
  line-height: 1.4;
  font-weight: 300;
  font-size: 16px;
  color: var(--text-color);



  direction: rtl;
  min-height: 100vh!important;
  /*height: 100%!important;*/
  width: 100%!important;
  display: flex;
  justify-content: center;
  background-color: var(--background);
}

#root {
  /*height: 100%!important;;*/min-height:100%;
}
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;font-family: 'Tajawal', monospace;
}

::-webkit-input-placeholder {
font-family: 'Tajawal', monospace;
  color: var(--text-color) !important;
  opacity: 0.5;
}

:-ms-input-placeholder {
font-family: 'Tajawal', monospace;
  color: var(--text-color) !important;
  opacity: 0.5;
}

::placeholder {
font-family: 'Tajawal', monospace;
  color: var(--text-color) !important;
  opacity: 0.5;
}

i,
i::after,
i::before {
  font-family: 'FontAwesome';
}

p {
font-family: 'Tajawal', monospace;
  font-weight: 400;
  /*color: #4c4c4c;*/
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: var(--text-color);
font-family: 'Tajawal', monospace;
  line-height: 1.2;
}
.button {
  background-color: var(--button-color) !important;
  color: var(--text-color) !important;
  border-color: var(--border-color) !important;
  transition: background-color 0.2s linear;font-family: 'Tajawal', monospace;
}
.button:hover {
  background-color: var(--button-hover-color);
  color: var(--text-color);
  border-color: var(--border-color);
}

/*MODES*/

/*Modal**************************************************************/
.modal-componenet {
  padding-left: 0.4rem !important;
  padding-right: 0.4rem !important;
}
.modal-componenet * {
  color: var(--text-color) !important;
}
.modal-componenet .modal-card {
  background-color: var(--container) !important;
  border-radius: 5px;
  border: 1px solid var(--border-color) !important;
}
.modal-componenet .modal-card-head {
  background-color: var(--background) !important;
  border: 1px solid var(--border-color) !important;
}
.modal-componenet .modal-card-body {
  background-color: var(--container) !important;
}
.modal-componenet .modal-card-foot {
  border: 1px solid var(--border-color) !important;
  background-color: var(--background) !important;
}

/*NAV BAR**************************************************************/

.navbar {
  border-top: 4px solid var(--border-color) !important;
  border-bottom: 2px solid var(--border-color) !important;
  width: 100% !important;
  z-index: 300 !important;
  background-color: var(--container) !important;
}
.navbar .navbar-menu {
  background-color: var(--container) !important;
}
.navbar * {
  color: var(--text-color);
}
.navbar .navbar-menu .navbar-link {
  background-color: var(--container) !important;
}

.navbar .navbar-menu .navbar-link:hover {
  background-color: var(--element-hover) !important;
}
.navbar .navbar-dropdown {
  background-color: var(--container) !important;
  border-top-color: var(--text-color) !important;
  /*border-bottom: 4px solid var(--border-color) !important;*/
}
.navbar .navbar-dropdown .navbar-item {
  color: var(--text-color) !important;
  font-weight: 500 !important;
}
.navbar .navbar-dropdown .navbar-divider {
  background-color: var(--border-color) !important;
}
.navbar .navbar-dropdown .navbar-item:hover {
  background-color: var(--element-hover) !important;
}
.navbar .navbar-dropdown .navbar-item.is-active {
  background-color: var(--primary) !important;
  font-weight: 600 !important;
}
/*
.navbar .navbar-dropdown .navbar-item * {
  background-color: var(--container);
  color: var(--text-color);
}*/
.navbar .navbar-item:hover {
  background-color: var(--container) !important;
}
.navbar .navbar-item:focus {
  background-color: var(--element-hover) !important;
}
.navbar .navbar-item.hvr:hover {
  background-color: var(--element-hover) !important;
}

.navbar-menu .field i {
  color: var(--border-color);
  transition: color ease 0.3s !important;
}
/*END NAV BAR**************************************************************/

/*PLAYER**************************************************************/
.player * {
  direction: ltr;
}
.player {
  padding-top: 3.8rem !important; /*4.2*/
  /*box-shadow: 0 1px 2px var(--box-shadow);*/

  max-width: 30rem; /*30*16 == 480*/
  width: 100%;

  /*height: 100%;*/
  background: var(--container);
}

.player-header {
  background: var(--container);
  max-width: 30rem; /*30*16 == 480*/
  position: fixed;
  /* center the element */
  right: 0;
  left: 0;
  margin-right: auto;
  margin-left: auto;
  width: 100%;
  z-index: 100;
  /* border-bottom: 1px solid var(--secondary);*/
  /* box-shadow: 0 1px 2px var(--box-shadow);*/
  border-left: 0.5px solid var(--border-color2);
  border-right: 0.5px solid var(--border-color2);
}
.player-body {
  margin-top: 20.8rem !important;
  /*border:1px solid #333;*/
  background: var(--background);

  max-width: 30rem;
  width: 100%;
  /*height: 100%;*/
  min-height: 100vh;
}
.player-header .title {
  padding-top: 1rem !important;
  font-size: 1.5rem !important;
  color: var(--text-color2) !important;
  transition: color ease 0.6s !important;
  margin-bottom: 0.2rem !important;
  line-height: 2rem !important;
}
.player-header .title.playing {
  font-size: 1.5rem !important;
  color: var(--primary) !important;
}

.player-header .title a {
  border-bottom: 1px solid var(--primary);
}
.player-header .title a .fa {
  color: var(--primary) !important;
}

.player-header .subtitle {
  font-size: 1rem !important;
  color: var(--text-color2) !important;
  transition: color ease 0.6s;
}
.player-header .subtitle.playing {
  font-size: 1rem !important;
  color: var(--text-color) !important;
}
.player-controls button {
  margin: 0 0.3rem;
  border-radius: 50%;
  box-shadow: none !important;
}
.player-controls button:focus {
  border-color: var(--border-color);
}
