.player-controls .play-btn .icon img {
  /**/
  max-width: 100%;
  border-radius: 50%;
  /*border: 1px solid var(--white) !important;*/
}

.player-controls .play-btn .icon {
  width: 8rem;
  height: 8rem;

  position: relative;
  cursor: pointer;
  transition: all ease 300ms;
}

.player-controls .play-btn:hover .icon {
  -webkit-transform: translateY(-10%) scale(1);
          transform: translateY(-10%) scale(1);
  z-index: 100;
}
.player-controls .play-btn::after,
.player-controls .play-btn::before {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;

  border-radius: 100%;
  transition: opcity ease 300ms, -webkit-transform;
  transition: transform, opcity ease 300ms;
  transition: transform, opcity ease 300ms, -webkit-transform;
}

.player-controls .play-btn .icon::after,
.player-controls .play-btn .icon::before {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  border-radius: 50%;

  transition: opcity ease 300ms, -webkit-transform;

  transition: transform, opcity ease 300ms;

  transition: transform, opcity ease 300ms, -webkit-transform;
}
.player-controls .play-btn .icon.pause:hover::after {
  content: '\f04b';
  font-family: 'FontAwesome';
  font-weight: 900;
  font-size: 2rem;
  display: grid;
  place-items: center;
  color: var(--white);
  background: var(--primary);
}
.player-controls .play-btn .icon.play:hover::after {
  content: '\f04c';
  font-family: 'FontAwesome';
  font-weight: 900;
  font-size: 2rem;
  display: grid;
  place-items: center;
  color: var(--white);
  background: var(--primary);
}
.player-controls .play-btn::before {
  background: var(--beat-color);
  -webkit-animation: pulse 900ms infinite;
          animation: pulse 900ms infinite;
}
.player-controls .play-btn:hover::before {
  background: transparent;
  -webkit-animation: none;
          animation: none;
}
.player-controls .play-btn:hover .icon::before {
  background: var(--primary);
  -webkit-animation: pulse 600ms infinite;
          animation: pulse 600ms infinite;
}

/*INPUTS**************************************************************/

input.search-nav-input {
  box-shadow: none !important;
  border-color: var(--border-color);
  background-color: var(--input-inner-bg);
  border-width: 2px;
  transition: border-color ease 0.3s;
  color: var(--text-color);
}
input.search-nav-input:focus {
  border-color: var(--primary);
  background-color: var(--input-inner-bg);
}
input.search-nav-input:focus ~ .icon i {
  color: var(--primary);
}
input.search-nav-input:hover {
  border-color: var(--primary);
  /*box-shadow: 0 0 0 1px red;*/
}
.navbar-item span.icon i.fa-close {
  cursor: pointer !important;
  pointer-events: all !important;
}
.navbar-item span.icon i.fa-close:hover {
  color: var(--primary);
}
input.search-nav-input:hover ~ .icon i {
  color: var(--primary);
}
/*END INPUTS**************************************************************/

/*EXTRA ELEMENT**************************************************************/
.anchor {
  display: block;
  position: relative;
  top: -27rem !important;
  visibility: hidden;
}

/*VOLUME ELEMENT**************************************************************/

.volume-container {
  margin: 0.5rem auto;
  text-align: center;
  cursor: pointer;
  position: relative;
  z-index: 2;
  border-bottom: 1px solid var(--border-color);
  border-top: 1px solid var(--border-color);
}
.volume-container .volume-button {
  height: 2rem;
  display: flex;
  align-items: center;
}
.volume-container .volume-button .volume {
  -webkit-transform: scale(0.7);
          transform: scale(0.7);
}
.volume-container .volume-slider {
  position: absolute;
  left: 2rem;
  top: 0.7rem;
  z-index: 12;
  width: 0;
  height: 0.5rem;

  background: var(--text-color);
  box-shadow: 0 0 20px var(rgb(0, 0, 0, 0.2));
  transition: 0.25s;
}
.volume-container .volume-slider .volume-percentage {
  background: var(--primary);
  height: 100%;
  width: 75%;
}
.volume-container:hover .volume-slider {
  /*left: -123px;*/
  width: 15rem;
}

/*TIME LINE**************************************************************/

.timeline {
  background: #eee;
  background: linear-gradient(
    77deg,
    rgba(45, 40, 135, 1) 0%,
    rgba(97, 97, 181, 1) 35%,
    rgba(0, 212, 255, 1) 100%
  );
  width: 100%;
  height: 0.5rem;
  position: relative;
  cursor: pointer;
  /*box-shadow: 0 1px 2px rgb(0,0,0,0.2);*/
}
.timeline .progress {
  /*background: #2b159e;*/
  background: rgb(45, 40, 135);
  background: var(--time-line-progress) !important;

  border-radius: 0;
  width: 0%;
  height: 100%;
  transition: width 0.3s ease-in-out;
}
.time {
  margin: 0.5rem 0;
  font-weight: 700;
  color: var(--text-color2);
}
.time .current {
  margin: 0 0.5rem;
}
.time .length {
  margin: 0 0.5rem;
}
.time.is-playing {
  margin: 0.5rem 0;
  font-weight: 700;
  color: var(--primary);
}
/*END PLAYER**************************************************************/

