}

.play-list-item {
  display: flex;
  align-items: center;

  width: 100%;
  color: var(--primary);
  /*border:1px solid red;*/
  position: relative;
  background: var(--container);
  box-shadow: 0 1px 2px var(--box-shadow);
  cursor: pointer;
  transition: background-color, color ease 0.6s;
}
.play-list-item.is-active-playing {
  background-color: var(--element-hover);
  color: var(--primary);
  border-right: 4px solid var(--primary);
}

.play-list-item:hover {
  background-color: var(--element-hover);
  color: var(--primary);
}
.play-list-item .options {
  padding: 1rem;
  position: static;
  right: 0;
  top: 0;
}
.play-list-item .dropdown-menu {
  z-index: 100 !important;
}
.play-list-item.selected {
  background-color: var(--primary);
