/*CUSTOM SELECT DROPDOWN*************************************************************/
.reciters-dropdown {
  width: 100%;
  /*border:1px solid red;*/
  margin-bottom: 0.9rem;
}
/* Dropdown Button */
.reciters-dropdown .dropbtn {
  background-color: var(--container);
  color: white;
  padding: 0.8rem;
  border-radius: 5px;
  font-size: 1rem;
  width: 20rem;
  border: none;
  cursor: pointer;
}

/* Dropdown button on hover & focus */
.reciters-dropdown .dropbtn:hover,
.dropbtn:focus {
  background-color: var(--element-hover);
}
.reciters-dropdown .dropbtn.active {
  background-color: var(--element-hover);
}
/* The container <div> - needed to position the dropdown content */
.reciters-dropdown .dropdown {
  position: relative;
  display: inline-block;
}

/* Dropdown Content (Hidden by Default) */

.reciters-dropdown .dropdown-content {
  display: none;
  position: absolute;
  left: 0;
  right: 0;
  margin-left: auto;

  background-color: var(--container);
  min-width: 160px;
  /*box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);*/
  z-index: 10;
}
.reciters-dropdown .dropdown-content .field {
  margin: 0 0.5rem 0.5rem;
}
.reciters-dropdown .dropdown-content .field input {
  direction: rtl !important;
}

/* Links inside the dropdown */
.reciters-dropdown .dropdown-content a {
  color: var(--text-color);
  padding: 0.8rem 1rem;
  text-decoration: none;
  display: block;
  text-align: center;
}

/* Change color of dropdown links on hover */
.reciters-dropdown .dropdown-content a:hover {
  background-color: var(--element-hover);
}

/* Show the dropdown menu (use JS to add this class to the .dropdown-content container when the user clicks on the dropdown button) */
.reciters-dropdown .show {
  display: block;
}

/*ANIMATIONS**************************************************************/
@-webkit-keyframes pulse {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 0.8;
  }
  100% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    opacity: 0;
  }
}
@keyframes pulse {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 0.8;
  }
  100% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    opacity: 0;
  }
}
@-webkit-keyframes pulse-sml {
  0% {
    -webkit-transform: scale(0.6);
            transform: scale(0.6);
    opacity: 0.8;
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 0;
  }
}
@keyframes pulse-sml {
  0% {
    -webkit-transform: scale(0.6);
            transform: scale(0.6);
    opacity: 0.8;
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 0;
  }
}
@-webkit-keyframes slide {
  0% {
    -webkit-transform: translateX(-25%);
            transform: translateX(-25%);
  }
  100% {
    -webkit-transform: translateX(25%);
            transform: translateX(25%);
  }
}
@keyframes slide {
  0% {
    -webkit-transform: translateX(-25%);
            transform: translateX(-25%);
  }
  100% {
    -webkit-transform: translateX(25%);
            transform: translateX(25%);
  }
}
@-webkit-keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@-webkit-keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes heart-pulse {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  50% {
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes heart-pulse {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  50% {
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

/*END ANIMATIONS**************************************************************/

