@import url('https://fonts.googleapis.com/css2?family=Tajawal:wght@300;400;500;700&display=swap');
html,
body {
  height: 100% !important;
}
:root {
  background-color: transparent;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth !important;
  font-family: 'Tajawal', monospace;
  height: 100% !important;
}

.light {
  /*Light mode variables*/
  --text-color: #2b2b2b;
  --text-color2: #c5c5c5;
  --button-color: #e4e6eb;

  --button-hover-color: #d8dadf;
  --element-hover: #ececec;
  --input-inner-bg: #f0f2f5;

  --border-color: rgb(218, 210, 210, 0.5);
  --border-color2: rgba(0, 0, 0, 0.1);
  --container: #fafafa;
  --primary: rgb(245, 18, 56);
  --time-line-progress: rgb(245, 18, 56);
  --box-shadow: rgba(0, 0, 0, 0.2);

  --white: #ffffff;
  --beat-color: #ddd;

  --background: #f0f2f5;
  --love: rgb(250, 9, 49);
  --love2: rgb(255, 0, 128);
}
.dark {
  /*Dark mode variables -*/
  --text-color: #fafafa;
  --text-color2: rgb(182, 182, 182);
  --element-hover: #3a3b3c;
  --input-inner-bg: #3a3b3c;
  --border-color: rgba(255, 255, 255, 0.1);
  --border-color2: rgba(255, 255, 255, 0.1);
  --button-color: #303031;
  --button-hover-color: #444546;
  --container: #242526;

  --primary: #398bf5;
  --time-line-progress: rgb(255, 0, 128);

  --box-shadow: rgba(0, 0, 0, 0.2);

  --white: #ffffff;
  --beat-color: rgb(88, 88, 88);

  --background: #18191a;

  --love: rgb(250, 9, 49);
  --love2: rgb(255, 0, 128);
}
.App {
  line-height: 1.4;
  font-weight: 300;
  font-size: 16px;
  color: var(--text-color);



  direction: rtl;
  min-height: 100vh!important;
  /*height: 100%!important;*/
  width: 100%!important;
  display: flex;
  justify-content: center;
  background-color: var(--background);
}

#root {
  /*height: 100%!important;;*/min-height:100%;
}